import React, { Component } from 'react';
import Card from './Card';
import CardGrid from './CardGrid';
import Section from './Section';
import duotoneLogo from '../../images/logos/duotone-logo.jpg';
import colorhexaLogo from '../../images/logos/colorhexa-logo.png';
import palettonLogo from '../../images/logos/paletton-logo.png';
import coolorsLogo from '../../images/logos/coolors-logo.png';

class Colors extends Component {
  render() {
    return (
      <Section title="Colors">
        <CardGrid>
          <Card
            cardImg={coolorsLogo}
            cardURL="https://coolors.co/"
            cardName="Coolors"
            cardDesc="The super fast color schemes generator! Create, save and share perfect palettes in seconds!"
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
          <Card
            cardImg="https://pbs.twimg.com/profile_images/630338320258609152/yqPjmY4D_400x400.png"
            cardURL="hthttp://themakers.io/tools/wp-content/uploads/2016/11/logo-1.giftp://www.colorhunt.co/"
            cardName="Color Hunt"
            cardDesc="Curated collection of beautiful colors, updated daily."
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
          <Card
            cardImg={palettonLogo}
            cardURL="http://paletton.com/"
            cardName="Paletton"
            cardDesc="A tool for creating color combinations that work together well."
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
          <Card
            cardImg="https://policyviz.com/wp-content/uploads/2018/02/Pigment_logo.jpg"
            cardURL="https://pigment.shapefactory.co/"
            cardName="Pigment"
            cardDesc="Find a fresh and vibrant color palette in seconds to kick off your next project."
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
          <Card
            cardImg={colorhexaLogo}
            cardURL="https://www.colorhexa.com/"
            cardName="ColorHexa"
            cardDesc="A tool that provides information about any color, including matching color schemes."
            imgHasBackground={true}
            cardCategory="Define your color palette"
          />
          <Card
            cardImg={duotoneLogo}
            cardURL="https://duotone.shapefactory.co/"
            cardName="Duotone"
            cardDesc="Find or create beautiful duotone images to use in your project."
            imgHasBackground={true}
            cardCategory="Apply color effects"
          />
        </CardGrid>
      </Section>
    );
  }
}

export default Colors;



