import React, { Component } from 'react';

class Section extends Component {
  render() {
    const filteredTitle = this.props.title.toLowerCase().replace(/\s/g, '-');
    return (
      <section className={`section section--${filteredTitle}`}>
        <header className="section__header">
          <h2 className="section__title">{this.props.title}</h2>
        </header>
        <div className="section__body">{this.props.children}</div>
      </section>
    );
  }
}

export default Section;



