import React, { Component } from 'react';
import Sidebar from './Sidebar';
import SearchBar from './SearchBar';
import FeatherIcon from './FeatherIcon';

const HeaderIcon = props => {
  return <FeatherIcon name={props.name} addClass="header__icon" />;
};

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navIsExpanded: false
    };
    this.handleToggleClick = this.handleToggleClick.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleToggleClick(e) {
    this.setState(state => ({
      navIsExpanded: !state.navIsExpanded
    }));
  }

  handleClick(e) {
    this.setState(state => ({
      navIsExpanded: false
    }));
    console.log(this.setState);
  }

  render() {
    return (
      <header
        className={`header${this.state.navIsExpanded && this.props.showNav ? ' expand-nav' : ''}`}
      >
        <div className="sidebar__canvas">
          {this.props.showToggler && (
            <button onClick={this.handleToggleClick} className="sidebar__btn--toggle">
              <HeaderIcon name="arrow-right" />
            </button>
          )}
          {this.props.showNav && <Sidebar />}
        </div>
        <div className="top-bar clearfix">
          <SearchBar />
        </div>
      </header>
    );
  }
}

export default Header;



