import React from 'react';
import { Link } from 'react-router-dom';
import FeatherIcon from './FeatherIcon';

const Logo = props => {
  return (
    <div className={`app-logo app-logo--${props.size} ${props.display}`}>
      <Link className="app-logo__link" to="/">
        <FeatherIcon name="bookmark" addClass="app-logo__icon" />
        <span className="app-name">Makerbook</span>
      </Link>
    </div>
  );
};

export default Logo;



