import React from 'react';
import jamIcons from '../../vendors/jam/jam-sprite.svg';

function JamIcon(props) {
  return (
    <svg
      className={`jam jam-${props.name} ${props.addClass || ''}`.trim()}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 24 24"
      width="24"
      height="24"
      fill="currentColor"
      stroke="none"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <use xlinkHref={`${jamIcons}#${props.name}`} />
    </svg>
  );
}

export default JamIcon;



