import React, { Component } from 'react';

class CardGrid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cardCount: 0
    };
  }
  componentDidMount() {
    this.setState({
      cardCount: React.Children.count(this.props.children)
    });
  }
  render() {
    const cardCountIsOdd = this.state.cardCount % 2 !== 0;
    return (
      <div className={`card-grid ${cardCountIsOdd ? 'has-odd-cards' : 'has-even-cards'}`}>
        {this.props.children}
      </div>
    );
  }
}

export default CardGrid;



