import React, { Component } from 'react';
import Card from './Card';
import CardGrid from './CardGrid';
import Section from './Section';
import pexelsLogo from '../../images/logos/pexels-logo.png';

class Photography extends Component {
  render() {
    return (
      <Section title="Photography">
        <CardGrid>
          <Card
            cardImg="https://upload.wikimedia.org/wikipedia/commons/e/ed/Logo_of_Unsplash.svg"
            cardURL="https://unsplash.com/"
            cardName="Unsplash"
            cardDesc="Free (do whatever you want) high-resolution stock photos."
            imgHasBackground={false}
            cardCategory="Find photos"
          />
          <Card
            cardImg="https://res-1.cloudinary.com/toolmuse/image/upload/v1491598093/production_Pixabay_lfjejo.png"
            cardURL="https://pixabay.com/"
            cardName="Pixabay"
            cardDesc="Over 410000 high quality photos, illustrations, and vector graphics. No attribution required."
            imgHasBackground={true}
            cardCategory="Find photos"
          />
          <Card
            cardImg={pexelsLogo}
            cardURL="https://www.pexels.com/"
            cardName="Pexels"
            cardDesc="Free high quality photos you can use everywhere. No attribution required."
            imgHasBackground={false}
            cardCategory="Find photos"
          />
        </CardGrid>
      </Section>
    );
  }
}

export default Photography;



