import React, { Component } from 'react';
import Card from './Card';
import CardGrid from './CardGrid';
import Section from './Section';

class Typography extends Component {
  render() {
    return (
      <Section title="Typography">
        <CardGrid>
          <Card
            cardImg="https://pbs.twimg.com/profile_images/742732476213268480/ZWREQYdH_400x400.jpg"
            cardURL="https://fonts.google.com/"
            cardName="Google Fonts"
            cardDesc="Hundreds of free, open-source fonts optimized for the web."
            imgHasBackground={true}
            cardCategory="Find fonts"
          />
          <Card
            cardImg="http://makerbook.net/wp-content/uploads/makerbook_thumbnail_font_squirrel-100x100.gif"
            cardURL="https://www.fontsquirrel.com/"
            cardName="Font Squirrel"
            cardDesc="Presents high-quality, designer-friendly, commercial-use fonts for easy downloading."
            imgHasBackground={true}
            cardCategory="Find fonts"
          />
          <Card
            cardImg="http://makerbook.net/wp-content/uploads/makerbook_thumbnail_dafont-100x100.gif"
            cardURL="https://www.dafont.com/"
            cardName="Dafont"
            cardDesc="Archive of freely downloadable fonts. Browse by alphabetical listing, style, author or popularity."
            imgHasBackground={true}
            cardCategory="Find fonts"
          />
          <Card
            cardImg="http://www.clker.com/cliparts/g/d/2/l/4/3/phi.svg"
            cardURL="https://grtcalculator.com/"
            cardName="GRT Calculator"
            cardDesc="A tool that calculates precise typographic proportions, including font size, line height, & associated spacing."
            imgHasBackground={false}
            cardCategory="Optimize your text"
          />
        </CardGrid>
      </Section>
    );
  }
}

export default Typography;



